*,*:before,*:after{box-sizing:border-box}body{width:100%;box-sizing:border-box;margin:0 auto;background-size:50%;box-sizing:border-box}.container{background-color:rgba(0,0,0,0.3);margin:0 auto;padding:10px}h1{position:fixed;z-index:1;top:0;left:0;right:0;padding:30px 0;color:white;text-align:center;text-transform:uppercase;margin:0;background-color:#1d4801;box-shadow:0px 20px 30px 3px rgba(0,0,0,0.55);text-shadow:0 0 10px #fff}
